<template>
  <v-container fluid class="down-top-padding">
    <v-row>
      <v-col cols="12" lg="4">
        <v-card>
          <v-card-text class="text-center pa-7">
              <v-data-table
    :loading="loading"
      :headers="headers"
      :items="lessons"
    >
     <template v-slot:item.edit="{ item }">
        <v-tooltip bottom>
      <template v-slot:activator="{ on, attrs }">
        <span @click="setEditLesson(item)">
          <v-icon
          color="primary"
          dark
          medium
          v-bind="attrs"
          v-on="on"
        >mdi-pencil-box-multiple</v-icon>
        </span>
      </template>
      <span>Dersi Düzenle</span>
        </v-tooltip>
         <v-tooltip bottom>
      <template v-slot:activator="{ on, attrs }">
        <span @click="editLesson={name:item.name, id:item.id};dialog3=true">
          <v-icon
          color="danger"
          dark
          medium
          v-bind="attrs"
          v-on="on"
        >mdi-close-box</v-icon>
        </span>
      </template>
      <span>Dersi Kaldır</span>
        </v-tooltip>
    </template>
       </v-data-table>
          </v-card-text>
        </v-card>
      </v-col>
      <v-col cols="12" lg="8">
        <v-card>
          <v-card-text>
            <h3 class="title blue-grey--text text--darken-2 font-weight-regular">{{$t('lesson.add')}}</h3>
            <h6 class="subtitle-2 font-weight-light">{{$t('lesson.info')}}</h6>
          </v-card-text>
          <v-divider></v-divider>
          <v-card-text>
           <v-text-field
              type="text"
              :error-messages="errormessages"
              v-model="lesson"
             :label="$t('lesson.add')"
              filled
              background-color="transparent"
            ></v-text-field>

            <v-btn class="text-capitalize mt-5 element-0" :loading="loading" :disabled="loading" @click="sendLesson()" color="success">{{$t('generate.submit')}}</v-btn>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
    <v-dialog v-model="dialog" persistent max-width="290">
      <v-card>
        <v-card-title class="headline">Ders Adı Güncelle</v-card-title>
        <v-card-text>
            <v-text-field
            @keypress.enter="setUpdateLesson()"
              type="teacher.phone"
              :error-messages="editerror"
              v-model="editLesson.name"
             :label="$t('lesson.update')"
              filled
              background-color="transparent"
            ></v-text-field>
            </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="green darken-1" text @click="dialog = false">İptal Et</v-btn>
          <v-btn color="green darken-1" text @click="setUpdateLesson()">Onayla</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <confirm :dialog="dialog3" text="Dersi Kaldırıyorsunuz. Emin misiniz?" @confirm="deleteLesson"/>
    <snack-bar :snackbar="snackbar"></snack-bar>
  </v-container>
</template>

<script>
export default {
 data(){
     return{
         lesson:"",
         dialog3:false,
         lessons:[],
         editLesson:{},
         dialog:false,
         loading:false,
         snackbar:false,
         errormessages:[],
         editerror:[],
         headers: [
          {
            text: "Ders Adı",
            align: 'start',
            filterable: true,
            value: 'name',
          },
          { text: 'Düzenle', value: 'edit' },
        ],
     }
 },
 methods:{
     sendLesson(){
       this.loading=true
       this.errormessages=[]
       if(this.lesson==''){
         this.errormessages.push('Ders Adı boş geçilemez.')
                 this.loading=false
       }
       else{
         this.$api.post('lessons',{name: this.lesson}).then(res=>{
             if(res.id){
                 this.lessons.unshift({name:this.lesson,id:res.id})
                 this.snackbar=true
                 this.lesson=""
       this.loading=false
                 setTimeout(() => {
                     this.snackbar=false
                 }, 4000);
             }
         })
       }

     },
     deleteLesson(){
      let pos = this.lessons.map(function(e) { return e.id; }).indexOf(this.editLesson.id);


         this.$api.put('lessons',this.editLesson.id,{isActive:0})
         this.lessons.splice(pos,1)
         this.dialog3=false
     },
     setEditLesson(row){
         this.editLesson = {name:row.name,id:row.id}
         this.dialog = true
     },
     setUpdateLesson(){
               this.loading=true
       if(this.editLesson.name==''){
         this.editerror.push('Ders Adı Boş geçilemez.')
                 this.loading=false
       }
       else{

           let pos = this.lessons.map(function(e) { return e.id; }).indexOf(this.editLesson.id);

         this.$api.put('lessons',this.editLesson.id,this.editLesson).then(result=>{
             if(result.result==1){
                 this.dialog=false
                 this.lessons[pos].name = this.editLesson.name
                 this.snackbar=true
                  this.loading=false
             }
             else{
               this.loading=false
               this.dialog=false
             }
         })
       }

     }
 },
 mounted(){
     this.$api.get('lessons').then(res=>{
         if(res.count!=0){
             this.lessons = res.body
         }
         else{
             this.lessons = []
         }
     })
 }

};
</script>